import { useForm } from "react-hook-form";

interface Tinput {
  nickName: string;
  email: string;
  name: string;
  phoneNumber: number;
  pw1: string;
  pw2: string;
}

const ReactHookForm = () => {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
    setError,
    reset,
  } = useForm<Tinput>({
    defaultValues: {
      email: "@naver.com",
    },
  });

  const onValid = (data: Tinput) => {
    if (data.pw1 !== data.pw2) {
      setError(
        "pw2",
        { message: "비밀번호가 다릅니다" },
        { shouldFocus: true }
      );
      return;
    }

    console.log(data);
    reset();
  };

  // console.log(errors);

  return (
    <>
      <form onSubmit={handleSubmit(onValid)}>
        <input
          {...register("nickName", {
            required: "별명을 적어주세요",
            minLength: { value: 2, message: "별명을 두글자 이상입니다." },
          })}
          placeholder="별명"
        />
        <span>{errors.nickName?.message}</span>
        <input
          {...register("email", {
            required: "이메일을 적어주세요",
            pattern: {
              value: /^[A-Za-z0-9.-_]+@naver.com$/,
              message: "네이버 메일을 적어주세요",
            },
          })}
          placeholder="이메일"
        />
        <span>{errors.email?.message}</span>
        <input
          {...register("name", { required: "이름을 적어주세요" })}
          placeholder="이름"
        />
        <span>{errors.name?.message}</span>
        <input
          {...register("phoneNumber", { required: "휴대폰 번호를 적어주세요" })}
          placeholder="휴대전화"
        />
        <span>{errors.phoneNumber?.message}</span>
        <input
          {...register("pw1", {
            required: "비밀번호를 적어주세요",
          })}
          placeholder="비밀번호"
        />
        <input
          {...register("pw2", {
            required: "비밀번호를 적어주세요",
            validate: {
              checkEqual: (value) =>
                value.length > 0 && value === watch()?.pw1 ? true : "❌",
            },
          })}
          placeholder="비밀번호 확인"
        />
        <span>
          {errors?.pw2?.message
            ? errors?.pw2?.message
            : watch()?.pw2?.length > 0
            ? watch("pw2") === watch("pw1")
              ? "✅"
              : "❌"
            : null}
        </span>
        <button>add</button>
      </form>
    </>
  );
};

export default ReactHookForm;
